(function() {
'use strict';
angular
    .module('angular')
    .controller('ProductsController', ProductsController);
    ProductsController.$inject = ['$location','Website','Products','$state','$scope','Helper'];
    function ProductsController($location, Website,Products,$state,$scope,Helper) {
    var vm = this;

    init();
    ///////////////
    function init() {

        vm.products = [];
        vm.activeFilters = [];
        vm.criteria = "";
        vm.brandBanner = "";
        vm.loadingContent = false;
        vm.showFiltersMobile = false;
        vm.itemsDisplayed = 9;
        vm.currency = {value: '', symbol: ''}; 
        vm.site = Helper.currentSite();
        vm.state = $state.$current.name;
        vm.loadingPage = true;
        vm.timestamp = new Date().getTime();
        if($state.params.s){
            vm.criteria = $state.params.s;
        }

        vm.setFilter = setFilter;
        vm.removeFilter = Website.removeFilter;
        vm.addMoreItems = addMoreItems;
        vm.saleProducts = saleProducts;

    }

    Website.broadcastUrlChanged($scope, function broadcastUpdate() {
        // Handle notification
        setTimeout(function(){
            if(vm.state=='keds'){
                loadKeds();
            } else {
                loadProducts();
            }
            $scope.$apply();
            });
    });

    function addMoreItems () {
        vm.itemsDisplayed += 3;
    }

    function saleProducts(prop, val){
        return function(item){
          if(vm.state=='sale'){
            return item[prop] > val;
          } else {
              return item;
          }
        }
    }

    function setFilter(filter,value){
        filter.criteria = value;
        Website.setFilter(filter);
        if(!filter.isActive){
            filter.isActive = true;
        }
    }

    function loadKeds(){
        Helper.currency().then(function (results) { 
            vm.currency = results;
            if(vm.currency.value=='USD'){vm.currency.symbol='$'} else {vm.currency.symbol=''};
            vm.loading = true;
            Products.keds().then(function (results) { vm.loading = false; vm.products = results; vm.loadingPage = false; },function(err){ Mail.errorLog(err); vm.loadingPage = false; });
         });
    }

    function loadProducts() {
        Helper.currency().then(function (results) { 
            vm.currency = results;
            if(vm.currency.value=='USD'){vm.currency.symbol='$'} else {vm.currency.symbol=''};
            vm.loading = true;
            var url = $location.url().substring(1);
            url = decodeURIComponent(url);
            url = url.replace("?","/");
            url = url.replace(/=/g,"/");
            url = url.replace(/&/g,"/");
            var filterArray = url.split('/');
            Products.list(filterArray).then(function (results) { vm.loading = false; vm.products = results; vm.loadingPage = false; },function(err){ Mail.errorLog(err); vm.loadingPage = false; });
            Products.filterList(filterArray).then(function (results) { vm.filters = results; },function(err){ Mail.errorLog(err) });
         });
    }

}
})();

(function() {
  'use strict';

  angular
    .module('angular')
    .controller('ProductDetailController', ProductDetailController);
  ProductDetailController.$inject = ['BlumeAnalytics','$stateParams','Products','Helper'];
  function ProductDetailController(BlumeAnalytics,$stateParams,Products,Helper) {
    var vm = this;
    
    init();
    ///////////////
    function init() {

      //Get Parameters
      vm.referenceCode = "";
      vm.colorCode = "";
      vm.isGrupoCachos = false;
      vm.getProductAvailability = getProductAvailability;
      if($stateParams.referenceCode){vm.referenceCode = $stateParams.referenceCode;}
      if($stateParams.colorCode){vm.colorCode = $stateParams.colorCode;}
      vm.currency = {value: '', symbol: ''}; 
      vm.site = Helper.currentSite();
      vm.timestamp = new Date().getTime();

      //Initialize Variables
      Helper.currency().then(function (results) { 
        vm.currency = results;
        if(vm.currency.value=='USD'){vm.currency.symbol='$'} else {vm.currency.symbol=''};
        Products.expand(vm.referenceCode,vm.colorCode).then(function(data){
          vm.products = data.products;
          vm.productImages = data.images;
          vm.product = vm.products[0];
          vm.option1 = vm.product.externalId;
          vm.relatedProducts = data.relatedProducts;
          // vm.colorVariations = data.colorVariations;
          vm.isGrupoCachos = data.isGrupoCachos;
          BlumeAnalytics.fbPixelViewContent(vm.product);
          vm.getProductAvailability();  
        });
     });
      
    }

    function getProductAvailability(){
      Products.availability(vm.option1).then(function(response){ vm.productAvailability = response; });
    }

      vm.setActive = function(index) {
        for (var i = 0; i < vm.productImages.length; i++) {
          vm.productImages[i].active=false;
        }
        vm.productImages[index].active=true;
      }

  }
})();

(function() {
    'use strict';

    angular
        .module('angular')
        .controller('PaymentNotificationController', PaymentNotificationController);
        PaymentNotificationController.$inject = ['Payments','$state'];
        function PaymentNotificationController(Payments,$state) {
        var vm = this;
        init();
        ///////////////

        function init() {
            //Initialize Controller
            vm.loading = false;
            vm.paymentData = {};
            if($state.$current.name=="paymentNotificationCredix"){
                Payments.receivePaymentNotificationCredix().then(function(response){ vm.paymentData = response; });
            } else if($state.$current.name=="paymentNotificationFttech"){
                Payments.receivePaymentNotificationFttech().then(function(response){ vm.paymentData = response; });
            } else if($state.$current.name=="paymentNotificationCybersource"){
                vm.paymentData = Payments.receivePaymentNotificationCybersource();
            } else if($state.$current.name=="paymentNotification"){
                vm.paymentData = Payments.receivePaymentNotification();
            }
        }

    }
})();
